
import { defineComponent, computed, ref, watch } from 'vue'
import { useStore } from 'vuex'
import { useState } from '@/hooks/useVuexHooks'
import * as types from '@/store/mutations-type'
import { formatTime } from '@/assets/ts/util'
import { PLAY_MODE } from '@/assets/ts/constant'
import useMode from './use-mode'
import useFavorite from './use-favorite'
import useMiddleInteractive from './use-middle-interactive'
import useCd from './use-cd'
import useLyric from './use-lyric'
import ProgressBar from './progress-bar.vue'
import Scroll from '@/components/base/scroll/scroll.vue'

export default defineComponent({
  name: 'player',
  components: {
    ProgressBar,
    Scroll
  },
  setup () {
    // data
    const audioRef = ref<any>(null)
    const currentTime = ref<number>(0)
    const songReady = ref<boolean>(false) // 是否准备好播放
    const progressChanging = ref<boolean>(false) // 是否在拖动进度条中

    // vuex ----------------------------------------------------------
    const store = useStore()
    const currentSong = computed(() => store.getters.currentSong)
    const { fullScreen, playing, currentIndex, playlist, playMode } = useState(
      '',
      ['fullScreen', 'playing', 'currentIndex', 'playlist', 'playMode']
    )

    // hooks
    const { modeIcon, changeMode } = useMode()
    const { getFavoriteIcon, toggleFavorite } = useFavorite()
    const { cdCls, cdRef, cdImageRef } = useCd()
    const {
      currentLyric,
      currentLineNum,
      lyricScrollRef,
      lyricListRef,
      pureMusicLyric,
      playLyric,
      playingLyric,
      stopLyric
    } = useLyric({
      songReady,
      currentTime
    })
    const {
      currentShow,
      middleLStyle,
      middleRStyle,
      onMiddleTouchStart,
      onMiddleTouchMove,
      onMiddleTouchEnd
    } = useMiddleInteractive()

    // computed --------------------------------------------------------
    // 设置播放按钮样式
    const playIcon = computed(() => {
      return playing.value ? 'icon-pause' : 'icon-play'
    })
    // 播放未准备样式
    const disableCls = computed(() => {
      return songReady.value ? '' : 'disable'
    })
    // 播放时间占总时长比
    const progress = computed(() => {
      return currentTime.value / currentSong.value.duration || 0
    })

    // watch  ----------------------------------------------------------
    // 监听歌曲切换，使用对应url播放
    watch(currentSong, (newSong) => {
      if (!newSong.url || !newSong.id) {
        return
      }
      // 切换歌曲重新计时
      currentTime.value = 0
      songReady.value = false
      const audioEl = audioRef.value
      audioEl.src = newSong.url
      // 调用播放方法
      audioEl.play()
      store.commit(types.SET_PLAYING_STATE, true)
    })
    // 监听播放暂停
    watch(playing, (newPlaying) => {
      if (!songReady.value) {
        return
      }
      const audioEl = audioRef.value
      if (newPlaying) {
        audioEl.play()
        playLyric()
      } else {
        audioEl.pause()
        stopLyric()
      }
    })

    // methds ---------------------------------------------------------
    // 取消全屏
    const goBack = () => {
      store.commit(types.SET_FULL_SCREEN, false)
    }
    // 切换播放暂停状态
    const togglePlay = () => {
      if (!songReady.value) {
        return
      }
      store.commit(types.SET_PLAYING_STATE, !playing.value)
    }
    // 在非交互情况下暂停播放，需处理，不然数据没暂停（歌词，进度条等），如合上电脑待机状态
    const pause = () => {
      store.commit(types.SET_PLAYING_STATE, false)
    }
    // 切换到上一首
    const prev = () => {
      const list = playlist.value
      if (!songReady.value || !list.length) {
        return
      }
      // 列表里只有一首歌
      if (list?.length === 1) {
        loop()
      } else {
        let index = currentIndex.value - 1
        // 当前已是第一首，则切换到最后一首
        if (index === -1) {
          index = list.length - 1
        }
        store.commit(types.SET_CURRENT_INDEX, index)
      }
    }
    // 切换到下一首
    const next = () => {
      const list = playlist.value
      if (!songReady.value || !list.length) {
        return
      }
      // 列表里只有一首歌
      if (list?.length === 1) {
        loop()
      } else {
        let index = currentIndex.value + 1
        // 当前已是最后一首，则切换到第一首
        if (index === list.length) {
          index = 0
        }
        store.commit(types.SET_CURRENT_INDEX, index)
      }
    }
    // 循环loop
    const loop = () => {
      const audioEl = audioRef.value
      // 重头开始播放
      audioEl.currentTime = 0
      audioEl.play()
      store.commit(types.SET_PLAYING_STATE, true)
    }
    // canplay：浏览器能够开始播放音频时触发
    const ready = () => {
      if (songReady.value) {
        return
      }
      songReady.value = true
      playLyric()
    }
    const error = () => {
      songReady.value = true
    }
    // 音频currentTime改变时触发
    const updateTime = (e: any) => {
      // 如果在拖动进度条，就不改变当前播放时间，拖动优先
      if (!progressChanging.value) {
        currentTime.value = e.target.currentTime
      }
    }
    // 拖动进度条中
    const onProgressChanging = (progress: number) => {
      progressChanging.value = true
      currentTime.value = currentSong.value.duration * progress
      // 拖动进度条的时候先同步歌词进度，再暂停播放歌词
      playLyric()
      stopLyric()
    }
    // 拖动进度条结束
    const onProgressChanged = (progress: number) => {
      progressChanging.value = false
      audioRef.value.currentTime = currentTime.value =
        currentSong.value.duration * progress
      if (!playing.value) {
        store.commit(types.SET_PLAYING_STATE, true)
      }
      // 拖动结束后再同步一次，并播放
      playLyric()
    }
    // 播放结束
    const end = () => {
      // 一首歌曲播放结束后，根据当前播放模式，播放下一首
      currentTime.value = 0
      if (playMode.value === PLAY_MODE.loop) {
        loop()
      } else {
        next()
      }
    }

    return {
      // data
      audioRef,
      formatTime,
      currentTime,
      playlist,

      // vuex
      fullScreen,
      currentSong,

      // computed
      playIcon,
      disableCls,
      progress,

      // methds
      goBack,
      togglePlay,
      pause,
      prev,
      next,
      ready,
      error,
      updateTime,
      onProgressChanging,
      onProgressChanged,
      end,
      // hooks -----------------
      // mode
      modeIcon,
      changeMode,
      // favorite
      getFavoriteIcon,
      toggleFavorite,
      // cd
      cdCls,
      cdRef,
      cdImageRef,
      // lyric
      currentLyric,
      currentLineNum,
      lyricScrollRef,
      lyricListRef,
      pureMusicLyric,
      playingLyric,
      // use-middle-interactive
      currentShow,
      middleLStyle,
      middleRStyle,
      onMiddleTouchStart,
      onMiddleTouchMove,
      onMiddleTouchEnd
    }
  }
})
